import { useTranslation } from "react-i18next";
import style from "./CustomCheckbox.module.css";
import { optionalSubjectCombineWithName } from "../../Constant/Admission/admission_constant";
import { Fragment } from "react";
import CreateButton from "../Button/CreateButton";
const GroupCustomCheckboxItem = ({
  options,
  onChange,
  defaultValue,
  customFormInputContainer,
  disabled,
  isCheck,
  parentKey,
  errorShow,
  errorShowKey,
  onMajorSubject,
  onNestedSubject,
  isGroupSelected,
  children,
}) => {
  const { t } = useTranslation();
  // console.info("this is defaul", defaultValue);
  return (
    <>
      <div
        className={style.form_input_container}
        style={customFormInputContainer}
      >
        {options?.map((ch, index) =>
          ch?.form_checklist_typo === "TEXT" ? (
            <div
              className={style.gcci_container}
              key={`${ch?.form_checklist_name}${index}`}
            >
              <label className={style.ccc}>
                <span className={style.form_input}>
                  {index + 1}). {ch?.form_checklist_name ?? ""}
                </span>
                <input type="checkbox" checked disabled />
                <span className={style.checkmark}></span>
              </label>
              {/* {defaultValue?.major_subject?.includes(
                ch?.form_checklist_subjectId
              ) ? (
                <CreateButton
                  label="dse"
                  onAction={() =>
                    onMajorSubject(parentKey, ch?.form_checklist_subjectId)
                  }
                  customStyle={{
                    margin: "0",
                    marginTop: "-9px",
                    padding: "0 0.8rem",
                    width: "fit-content",
                    backgroundColor: "#001a72",
                    color: "#ffffff",
                  }}
                />
              ) : defaultValue &&
                defaultValue["max_major_subject"] > 0 &&
                isGroupSelected ? (
                <CreateButton
                  label="dse"
                  onAction={() =>
                    onMajorSubject(parentKey, ch?.form_checklist_subjectId)
                  }
                  customStyle={{
                    margin: "0",
                    marginTop: "-9px",
                    padding: "0 0.8rem",
                    width: "fit-content",
                  }}
                />
              ) : null} */}
            </div>
          ) : ch?.form_checklist_typo === "SELECT_GROUP" ? (
            <Fragment key={`${ch?.form_checklist_name}${index}`}>
              {" "}
              <label className={style.ccc}>
                <span className={style.form_input}>
                  {index + 1}). {ch?.form_checklist_name ?? ""}
                  {errorShow && errorShow[`${errorShowKey}${index}`] ? (
                    <span
                      style={{
                        color: "red",
                        marginLeft: "15px",
                      }}
                    >
                      * {errorShow[`${errorShowKey}${index}`]}
                    </span>
                  ) : null}
                </span>
                <br />
                <div
                  style={{
                    display: "block",
                    marginBottom: "0.4rem",
                  }}
                ></div>

                <input
                  type="checkbox"
                  checked={
                    isCheck
                      ? defaultValue &&
                        defaultValue[index] &&
                        defaultValue[index]["selected"]
                      : false
                  }
                  onChange={(e) =>
                    onChange(e, parentKey, index, "dummy", {
                      min: ch?.form_checklist_rule,
                      max: ch?.form_checklist_rule_max,
                      or_subject: "FIXED",
                      fixedSubject: ch?.form_checklist_typo_option_pl,
                      maxCount: ch?.form_checklist_major_select_max,
                      nestedMaxCount: ch?.form_checklist_nested_select_min,
                    })
                  }
                  disabled={disabled}
                />
                <span className={style.checkmark}></span>
              </label>
              {ch?.form_checklist_typo_option_pl?.map((ft, ft_index) => (
                <div
                  className={style.gcci_container}
                  key={`${ft?.subjectNam}${ft_index}`}
                  style={{
                    marginLeft: "1.4rem",
                  }}
                >
                  <label
                    className={style.ccc}
                    style={{
                      display: "block",
                    }}
                  >
                    <span className={style.form_input}>
                      {ft_index + 1}). {ft?.subjectName ?? ""}
                    </span>
                  </label>
                  {defaultValue &&
                  defaultValue[index] &&
                  defaultValue[index]?.major_subject?.includes(ft?._id) ? (
                    <CreateButton
                      label="dse"
                      onAction={() => onMajorSubject(parentKey, index, ft?._id)}
                      customStyle={{
                        margin: "0",
                        marginTop: "-9px",
                        padding: "0 0.8rem",
                        width: "fit-content",
                        backgroundColor: "#001a72",
                        color: "#ffffff",
                      }}
                    />
                  ) : isGroupSelected &&
                    defaultValue &&
                    defaultValue[index] &&
                    defaultValue[index]["max_major_subject"] > 0 &&
                    defaultValue[index]["selected"] ? (
                    <CreateButton
                      label="dse"
                      onAction={() => onMajorSubject(parentKey, index, ft?._id)}
                      customStyle={{
                        margin: "0",
                        marginTop: "-9px",
                        padding: "0 0.8rem",
                        width: "fit-content",
                      }}
                    />
                  ) : null}

                  {defaultValue &&
                  defaultValue[index] &&
                  defaultValue[index]?.nested_subject?.includes(ft?._id) ? (
                    <CreateButton
                      label={ch?.form_checklist_nested_select_name ?? ""}
                      onAction={() =>
                        onNestedSubject(parentKey, index, {
                          sub_id: ft?._id,
                        })
                      }
                      customStyle={{
                        margin: "0",
                        marginTop: "-9px",
                        padding: "0 0.8rem",
                        width: "fit-content",
                        backgroundColor: "#001a72",
                        color: "#ffffff",
                      }}
                    />
                  ) : isGroupSelected &&
                    defaultValue &&
                    defaultValue[index] &&
                    defaultValue[index]["nested_select_min"] > 0 &&
                    defaultValue[index]["selected"] ? (
                    <CreateButton
                      label={ch?.form_checklist_nested_select_name ?? ""}
                      onAction={() =>
                        onNestedSubject(parentKey, index, {
                          sub_id: ft?._id,
                        })
                      }
                      customStyle={{
                        margin: "0",
                        marginTop: "-9px",
                        padding: "0 0.8rem",
                        width: "fit-content",
                      }}
                    />
                  ) : null}
                </div>
              ))}
            </Fragment>
          ) : (
            <Fragment key={`${ch?.form_checklist_name}${index}`}>
              <label className={style.ccc}>
                <span className={style.form_input}>
                  {index + 1}). {ch?.form_checklist_name ?? ""}
                  {errorShow && errorShow[`${errorShowKey}${index}`] ? (
                    <span
                      style={{
                        color: "red",
                        marginLeft: "15px",
                      }}
                    >
                      * {errorShow[`${errorShowKey}${index}`]}
                    </span>
                  ) : null}
                </span>
                <br />
                <div
                  style={{
                    display: "block",
                    marginBottom: "0.4rem",
                  }}
                ></div>
              </label>
              {ch?.form_checklist_typo_option_pl?.map((ft, ft_index) => (
                <div
                  className={style.gcci_container}
                  key={`${ft?.subjectNam}${ft_index}`}
                >
                  <label
                    className={style.ccc}
                    style={{
                      display: "block",
                      marginLeft: "3.6rem",
                    }}
                  >
                    <span className={style.form_input}>
                      {ft_index + 1}). {ft?.subjectName ?? ""}
                    </span>
                    <input
                      type="checkbox"
                      checked={
                        isCheck
                          ? defaultValue &&
                            defaultValue[index] &&
                            defaultValue[index][ft_index] &&
                            defaultValue[index][ft_index]["selected"]
                          : false
                      }
                      onChange={(e) =>
                        onChange(e, parentKey, index, ft_index, {
                          min: ch?.form_checklist_rule,
                          max: ch?.form_checklist_rule_max,
                          masterId: ft?._id,
                          maxCount: ch?.form_checklist_major_select_max,
                          nestedMaxCount: ch?.form_checklist_nested_select_min,
                        })
                      }
                      disabled={disabled}
                    />
                    <span className={style.checkmark}></span>
                  </label>
                  {defaultValue &&
                  defaultValue[index] &&
                  defaultValue[index]?.major_subject?.includes(ft?._id) ? (
                    <CreateButton
                      label="dse"
                      onAction={() => onMajorSubject(parentKey, index, ft?._id)}
                      customStyle={{
                        margin: "0",
                        marginTop: "-9px",
                        padding: "0 0.8rem",
                        width: "fit-content",
                        backgroundColor: "#001a72",
                        color: "#ffffff",
                      }}
                    />
                  ) : isGroupSelected &&
                    defaultValue &&
                    defaultValue[index] &&
                    defaultValue[index]["max_major_subject"] > 0 &&
                    defaultValue[index][ft_index] &&
                    defaultValue[index][ft_index]["selected"] ? (
                    <CreateButton
                      label="dse"
                      onAction={() => onMajorSubject(parentKey, index, ft?._id)}
                      customStyle={{
                        margin: "0",
                        marginTop: "-9px",
                        padding: "0 0.8rem",
                        width: "fit-content",
                      }}
                    />
                  ) : null}
                  {defaultValue &&
                  defaultValue[index] &&
                  defaultValue[index]?.nested_subject?.includes(ft?._id) ? (
                    <CreateButton
                      label={ch?.form_checklist_nested_select_name ?? ""}
                      onAction={() =>
                        onNestedSubject(parentKey, index, {
                          sub_id: ft?._id,
                        })
                      }
                      customStyle={{
                        margin: "0",
                        marginTop: "-9px",
                        padding: "0 0.8rem",
                        width: "fit-content",
                        backgroundColor: "#001a72",
                        color: "#ffffff",
                      }}
                    />
                  ) : isGroupSelected &&
                    defaultValue &&
                    defaultValue[index] &&
                    defaultValue[index]["nested_select_min"] > 0 &&
                    defaultValue[index][ft_index] &&
                    defaultValue[index][ft_index]["selected"] ? (
                    <CreateButton
                      label={ch?.form_checklist_nested_select_name ?? ""}
                      onAction={() =>
                        onNestedSubject(parentKey, index, {
                          sub_id: ft?._id,
                        })
                      }
                      customStyle={{
                        margin: "0",
                        marginTop: "-9px",
                        padding: "0 0.8rem",
                        width: "fit-content",
                      }}
                    />
                  ) : null}
                </div>
              ))}
              {ch?.form_checklist_typo_option_pl_optional?.length > 0 ? (
                <>
                  {ch?.form_checklist_typo_option_pl_optional?.map(
                    (rft, r_index) => (
                      <Fragment key={`rft${r_index}`}>
                        <label
                          className={style.ccc}
                          style={{
                            display: "block",
                            marginLeft: "3.6rem",
                          }}
                        >
                          <span className={style.form_input}>
                            {ch?.form_checklist_typo_option_pl?.length +
                              1 +
                              r_index}
                            ).
                            {optionalSubjectCombineWithName(rft?.options)}
                            {errorShow &&
                            errorShow[
                              `${errorShowKey}${index}${r_index}${ch?.form_checklist_typo_option_pl?.length}OR`
                            ] ? (
                              <span
                                style={{
                                  color: "red",
                                  marginLeft: "15px",
                                }}
                              >
                                *{" "}
                                {
                                  errorShow[
                                    `${errorShowKey}${index}${r_index}${ch?.form_checklist_typo_option_pl?.length}OR`
                                  ]
                                }
                              </span>
                            ) : null}
                          </span>

                          <input
                            type="checkbox"
                            checked={
                              isCheck
                                ? defaultValue &&
                                  defaultValue[index] &&
                                  defaultValue[index][
                                    `${r_index}${ch?.form_checklist_typo_option_pl?.length}OR`
                                  ] &&
                                  defaultValue[index][
                                    `${r_index}${ch?.form_checklist_typo_option_pl?.length}OR`
                                  ]["selected"]
                                : false
                            }
                            onChange={(e) =>
                              onChange(
                                e,
                                parentKey,
                                index,
                                `${r_index}${ch?.form_checklist_typo_option_pl?.length}OR`,
                                {
                                  min: ch?.form_checklist_rule,
                                  max: ch?.form_checklist_rule_max,
                                  masterId: "",
                                  maxCount: ch?.form_checklist_major_select_max,
                                  nestedMaxCount:
                                    ch?.form_checklist_nested_select_min,
                                }
                              )
                            }
                            disabled={disabled}
                          />
                          <span className={style.checkmark}></span>
                        </label>

                        {defaultValue &&
                        defaultValue[index] &&
                        defaultValue[index][
                          `${r_index}${ch?.form_checklist_typo_option_pl?.length}OR`
                        ] &&
                        defaultValue[index][
                          `${r_index}${ch?.form_checklist_typo_option_pl?.length}OR`
                        ]["selected"] ? (
                          <>
                            {rft?.options?.map((ft, ft_index) => (
                              <div
                                className={style.gcci_container}
                                key={`${ft?.subjectNam}${ft_index}`}
                              >
                                <label
                                  className={style.ccc}
                                  style={{
                                    display: "block",
                                    marginLeft: "5.9rem",
                                  }}
                                >
                                  <span className={style.form_input}>
                                    {ft_index + 1}). {ft?.subjectName ?? ""}
                                  </span>
                                  <input
                                    type="checkbox"
                                    checked={
                                      isCheck
                                        ? // ? defaultValue &&
                                          //   defaultValue[index] &&
                                          //   defaultValue[index][
                                          //     ft_index +
                                          //       ch?.form_checklist_typo_option_pl
                                          //         ?.length
                                          //   ] &&
                                          //   defaultValue &&
                                          //   defaultValue[index][
                                          //     ft_index +
                                          //       ch?.form_checklist_typo_option_pl
                                          //         ?.length
                                          //   ]["parentIndex"] === r_index &&
                                          //   defaultValue &&
                                          //   defaultValue[index][
                                          //     ft_index +
                                          //       ch?.form_checklist_typo_option_pl
                                          //         ?.length
                                          //   ]["selected"]

                                          defaultValue &&
                                          defaultValue[index] &&
                                          defaultValue[index][
                                            `${r_index}${
                                              ft_index +
                                              ch?.form_checklist_typo_option_pl
                                                ?.length
                                            }`
                                          ] &&
                                          defaultValue[index][
                                            `${r_index}${
                                              ft_index +
                                              ch?.form_checklist_typo_option_pl
                                                ?.length
                                            }`
                                          ]["selected"]
                                        : false
                                    }
                                    onChange={(e) =>
                                      onChange(
                                        e,
                                        parentKey,
                                        index,
                                        `${r_index}${
                                          ft_index +
                                          ch?.form_checklist_typo_option_pl
                                            ?.length
                                        }`,
                                        {
                                          min: ch?.form_checklist_rule,
                                          max: ch?.form_checklist_rule_max,
                                          masterId: ft?._id,
                                          or_subject: "YES",
                                          parentIndex: r_index,
                                          parentWholeKey: `${r_index}${ch?.form_checklist_typo_option_pl?.length}OR`,
                                          maxCount:
                                            ch?.form_checklist_major_select_max,
                                          nestedMaxCount:
                                            ch?.form_checklist_nested_select_min,
                                        }
                                      )
                                    }
                                    disabled={disabled}
                                  />
                                  <span className={style.checkmark}></span>
                                </label>
                                {defaultValue &&
                                defaultValue[index] &&
                                defaultValue[index]?.major_subject?.includes(
                                  ft?._id
                                ) ? (
                                  <CreateButton
                                    label="dse"
                                    onAction={() =>
                                      onMajorSubject(parentKey, index, ft?._id)
                                    }
                                    customStyle={{
                                      margin: "0",
                                      marginTop: "-9px",
                                      padding: "0 0.8rem",
                                      width: "fit-content",
                                      backgroundColor: "#001a72",
                                      color: "#ffffff",
                                    }}
                                  />
                                ) : isGroupSelected &&
                                  defaultValue &&
                                  defaultValue[index] &&
                                  defaultValue[index][
                                    `${r_index}${
                                      ft_index +
                                      ch?.form_checklist_typo_option_pl?.length
                                    }`
                                  ] &&
                                  defaultValue[index][
                                    `${r_index}${
                                      ft_index +
                                      ch?.form_checklist_typo_option_pl?.length
                                    }`
                                  ]["selected"] &&
                                  defaultValue[index]["max_major_subject"] >
                                    0 ? (
                                  <CreateButton
                                    label="dse"
                                    onAction={() =>
                                      onMajorSubject(parentKey, index, ft?._id)
                                    }
                                    customStyle={{
                                      margin: "0",
                                      marginTop: "-9px",
                                      padding: "0 0.8rem",
                                      width: "fit-content",
                                    }}
                                  />
                                ) : null}
                                {defaultValue &&
                                defaultValue[index] &&
                                defaultValue[index]?.nested_subject?.includes(
                                  ft?._id
                                ) ? (
                                  <CreateButton
                                    label={
                                      ch?.form_checklist_nested_select_name ??
                                      ""
                                    }
                                    onAction={() =>
                                      onNestedSubject(parentKey, index, {
                                        sub_id: ft?._id,
                                      })
                                    }
                                    customStyle={{
                                      margin: "0",
                                      marginTop: "-9px",
                                      padding: "0 0.8rem",
                                      width: "fit-content",
                                      backgroundColor: "#001a72",
                                      color: "#ffffff",
                                    }}
                                  />
                                ) : isGroupSelected &&
                                  defaultValue &&
                                  defaultValue[index] &&
                                  defaultValue[index][
                                    `${r_index}${
                                      ft_index +
                                      ch?.form_checklist_typo_option_pl?.length
                                    }`
                                  ] &&
                                  defaultValue[index][
                                    `${r_index}${
                                      ft_index +
                                      ch?.form_checklist_typo_option_pl?.length
                                    }`
                                  ]["selected"] &&
                                  defaultValue[index]["nested_select_min"] >
                                    0 ? (
                                  <CreateButton
                                    label={
                                      ch?.form_checklist_nested_select_name ??
                                      ""
                                    }
                                    onAction={() =>
                                      onNestedSubject(parentKey, index, {
                                        sub_id: ft?._id,
                                      })
                                    }
                                    customStyle={{
                                      margin: "0",
                                      marginTop: "-9px",
                                      padding: "0 0.8rem",
                                      width: "fit-content",
                                    }}
                                  />
                                ) : null}
                              </div>
                            ))}
                          </>
                        ) : null}
                      </Fragment>
                    )
                  )}
                </>
              ) : null}
            </Fragment>
          )
        )}
      </div>
    </>
  );
};

export default GroupCustomCheckboxItem;
